import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";

import axios from "axios";
import Units from "./components/Units";
import Form from "./components/Form";
import Home from "./components/Home";
import Login from "./components/Login";
import NoMatch from "./NoMatch";

import "./App.css";

class App extends Component {
  state = {
    units: [],
    user: null,
  };

  addUnit = (code, title, offering) => {
    const newUnit = {
      code,
      title,
      offering: offering,
    };
    axios
      .post("/api/units", newUnit)
      .then((res) => {
        this.setState({ units: res.data });
      })
      .catch((err) => console.error(err));
  };

  setUser = (username, password) => {
    const user = {
      username,
      password,
    };
    axios
      .post("/api/login", user)
      .then((res) => {
        console.log(res.data);
        this.setState({ user: res.data.name });
      })
      .catch((err) => console.error(err));
  };

  componentDidMount() {
    axios
      .get("/api/units")
      .then((res) => {
        this.setState({ units: res.data });
      })
      .catch((err) => {
        console.error(err);
      });
  }

  deleteUnit = (id) => {
    axios
      .delete("/api/units?id=" + id)
      .then((res) => {
        this.setState({ units: res.data });
      })
      .catch((err) => console.error(err));
  };
  logout = () => {
    this.setState({ user: null });
    console.log("Logout Successfully");
  };

  render() {
    return (
      <Router>
        <div className="navigation">
          <div className="navlink">
            <Link to="/">Home</Link>
            <Link to="/units">Units</Link>
            <Link to="/add-units">UnitForm</Link>

            {this.state.user ? (
              <>
                <span>{this.state.user} </span>
                <button onClick={this.logout} className="button-primary">
                  Logout
                </button>
              </>
            ) : (
              <Link to="/login">LoginForm</Link>
            )}
          </div>
        </div>
        <Switch>
          <Route exact path="/">
            <Home user={this.state.user} />
          </Route>
          <Route path="/units">
            <Units units={this.state.units} deleteUnit={this.deleteUnit} />
          </Route>
          <Route path="/add-units">
            <Form addUnit={this.addUnit} user={this.state.user} />
          </Route>
          <Route path="/login">
            <Login user={this.state.user} setUser={this.setUser} />
          </Route>
          <Route component={NoMatch} />
        </Switch>
      </Router>
    );
  }
}

export default App;
